import React from 'react';



export const Info = ({item}) => {

    
    return(
        <>
        {
            item.map((itemval, ind) => {
                return (
                    <>
                        <div className="alignment" key={ind}>
                                <li>{itemval.name}</li>
                                <li>{itemval.age}</li>
                                <li>{itemval.option}</li>
                        </div>
                    </>
                )
            })
        }
        </>
    )
}