import { useReducer } from "react";
import DataContext from "./dataContext";
import dataReducer from "./dataReducer";
import {GET_CART} from './types';
import A from "../Location/Delhi/Delhi.jpg";
import B from "../Location/Amritsar/Amritsar.JPG";
import C from "../Location/Jaipur/Jaipur.jpg";
import AA from "../Location/Travels/a.jpg";
import BB from "../Location/Travels/b.jpg";
import CC from "../Location/Travels/c.jpg";
import DD from "../Location/Travels/d.jpg";


const DataState = (props) => {
    
    const initialState = {

        menu: [
            {
                imgsrc: A,
                package_name: "Delhi Package",
                time: "7 DAYS / 6 NIGHTS",
                desc: "lorem ipsum simplelorem ipsum simplelorem ipsum simplelorem ipsum simple",
                book: "BOOK",
                price: 39990,
            },
            {
                imgsrc: C   ,
                package_name: "Jaipur Package",
                time: "3 DAYS / 2 NIGHTS",
                desc: "lorem ipsum simplelorem ipsum simplelorem ipsum simplelorem ipsum simple",
                book: "BOOK",
                price: 22999,
            },
            {
                imgsrc: B,
                package_name: "Amritsar Package",
                time: "3 DAYS / 2 NIGHTS",
                desc: "lorem ipsum simplelorem ipsum simplelorem ipsum simplelorem ipsum simple",
                book: "BOOK",
                price: 35999,
            },
        ],
        bestLocation: [
            {
                imgsrc: AA,
                name: "Waste Of Wonder"
            },
            {
                imgsrc: BB,
                name: "Agrasen Ki Baoli"
            },
            {
                imgsrc: CC,
                name: "Hawa Mahal"
            },
            {
                imgsrc: DD,
                name: "Amer Fort"
            },
        ],
        kart: []
    }

    const [state, dispatch] = useReducer(dataReducer, initialState);
    const getCart = (cart) =>{
        dispatch({type: GET_CART, payload: cart})
    }
    
    return (
        <DataContext.Provider value={{bestLocation: state.bestLocation, kart: state.kart, menu: state.menu,  getCart}}>
            {props.children}
        </ DataContext.Provider>
    )
}

export default DataState;
